    font-display: swap;
    src: url('../fonts/Nunitosanslight.woff2') format('woff2'), url('../fonts/Nunitosanslight.woff') format('woff'), url('../fonts/Nunitosanslight.ttf') format('truetype');
}

@font-face {
    font-family: 'Nunito Sans';
    font-weight: 400;
